export default {
  name: 'customdialog',
  props: {
    title: {
      type: String,
      default: '标题'
    },
	isShow:{
		type: Boolean,
		default: false
	},
	cancelText:{
		type: String,
		default: '取消'
	},
	confirmText:{
		type: String,
		default: '确认'
	}
  },
  data() {
    return {
      isMask: true,
      callback: null
    }
  },
  methods: {
    doConfirm() {
		this.$emit('doConfirm','')
    },
	doCancel(){
		this.$emit('doCancel','')
	}
  }
}
